

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  /* 62.5% = 10px */
  font-size: 62.5%;
}

.heading {
  font-size: 5rem;
  color: white;
  font-weight: 400;
  padding: 1rem;
  text-transform: uppercase;
}

.container {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.form__container {
  height: 60rem;
  margin: 5rem auto;
  width: 60%;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.form__container:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 37.5em) {
  html {
    font-size: 42%;
  }
}

.btn {
  text-transform: uppercase;
  text-decoration: none;
  background: none;
  padding: 1rem 2rem;
  border: none;
  outline: none;
  height: 6rem;
  width: 16rem;
  display: inline-block;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  position: relative;
  cursor: pointer;
  margin: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.btn__text {
  font-size: 1.6rem;
  color: blue;
}

.btn:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.delete {
  height: 5rem;
  width: 10rem;
}

.delete__text {
  color: red;
}


@media only screen and (max-width: 50em) {
  .btn {
    height: 4rem;
    width: 8rem;
    padding : .3rem .2rem;
  }
  .btn__text {
    font-size: 0.6rem;
  }
  .delete {
    height: 4rem;
    width: 8rem;
  }
}




@media only screen and (max-width: 75em) {
  .btn {
    height: 4rem;
    width: 12rem;
  }
  .btn__text {
    font-size: 0.9rem;
  }
  .delete {
    height: 4rem;
    width: 10rem;
  }
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.form__values {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 5rem;
  width: 80%;
  margin: 3.5rem;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.form__input {
  font-size: 2rem;
  font-family: inherit;
  color: black;
  padding: 1.5rem 2rem;
  border: none;
  border-bottom: 1px solid black;
  margin: 2rem;
  width: 90%;
}

.form__input:focus {
  outline: none;
  box-shadow: 0 1rem 2rem rgba(black, 0.1);
  border-bottom: 1px solid blue;
}

.form__label {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 100;
  color: blue;
  letter-spacing: 1.2;
}

.input__data {
  font-size: 2rem;
  color: blue;
  margin: 2rem 6rem;
}



.error {
  color: red;
  text-transform: uppercase;
  display: inline-block;
  margin: 2rem;
  font-weight: 200;
  font-size: 3rem;
}

.contact {
  margin: 3rem auto;
  height: 15rem;
  width: 60%;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.contact:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.contact__text{
  font-size: 2.3rem;
  color :  blue;
  font-weight:  200;
}


@media only screen and (max-width: 75em) {
     .contact__text{
        font-size: 1.4rem;
     }
}
.home {
  margin: 2rem auto;
  width: 100%;
  height: 80vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.home__heading {
  font-size: 1.8rem;
  text-transform: uppercase;
  color: blue;
  margin: 1.06rem;
  padding: 4rem;
  text-align: center;
  width: 100%;
}

.header {
  margin: 0;
  background-image: -webkit-linear-gradient(left, red, orange);
  background-image: linear-gradient(to right, red, orange);
  width: 100%;
  height: 9rem;
  text-align: center;
}

